import { auth } from "@/auth"
import { getReportData } from "@/actions/time-actions"
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from "@/components/ui/table"
import { format } from "date-fns"
import { BarChart3, PieChart, Users, DollarSign } from "lucide-react"

export default async function ReportsPage({ searchParams }: { searchParams: Promise<{ year?: string }> }) {
    const session = await auth()
    if (!session?.user?.id) return null

    const resolvedParams = await searchParams
    // Date Range Logic (Default to current Year for "This Year" view as per request, or custom)
    const year = Number(resolvedParams.year) || new Date().getFullYear()
    const from = new Date(year, 0, 1)
    const to = new Date(year, 11, 31, 23, 59, 59)

    const data = await getReportData(from, to)

    if ("error" in data) {
        return <div className="p-12 text-center text-red-500">Error loading reports: {data.error}</div>
    }

    const { summary, entries } = data

    // Group entries by User for the Team Table
    const teamStats = new Map<string, {
        name: string,
        totalHours: number,
        billableHours: number,
        billableAmount: number,
        utilization: number
    }>()

    entries.forEach(e => {
        const userId = e.userId
        const userName = e.user.name || "Unknown"
        const hours = e.durationSeconds / 3600
        const rate = e.project?.billableRate ?? e.user.billableRate ?? 0
        const isBillable = e.isBillable && (e.project?.isBillable ?? true)

        const current = teamStats.get(userId) || { name: userName, totalHours: 0, billableHours: 0, billableAmount: 0, utilization: 0 }

        current.totalHours += hours
        if (isBillable) {
            current.billableHours += hours
            current.billableAmount += hours * rate
        }

        teamStats.set(userId, current)
    })

    // Calculate utilization per user
    Array.from(teamStats.values()).forEach(stat => {
        stat.utilization = stat.totalHours > 0 ? (stat.billableHours / stat.totalHours) * 100 : 0
    })

    return (
        <div className="space-y-8 max-w-6xl mx-auto">
            <div className="flex items-center justify-between">
                <div>
                    <h1 className="text-3xl font-bold tracking-tight">Reports: {year}</h1>
                    <p className="text-muted-foreground">Performance and billing overview.</p>
                </div>
            </div>

            {/* Summary Cards */}
            <div className="grid gap-4 md:grid-cols-4">
                <Card>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium">Total Hours</CardTitle>
                        <BarChart3 className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold">{summary.totalHours.toFixed(2)}</div>
                    </CardContent>
                </Card>
                <Card>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium">Billable Hours</CardTitle>
                        <PieChart className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold">{summary.billableHours.toFixed(2)}</div>
                        <p className="text-xs text-muted-foreground">
                            {summary.utilization.toFixed(1)}% Utilization
                        </p>
                    </CardContent>
                </Card>
                <Card>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium">Billable Amount</CardTitle>
                        <DollarSign className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold">${summary.billableAmount.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</div>
                    </CardContent>
                </Card>
                <Card>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium">Uninvoiced</CardTitle>
                        <DollarSign className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                        {/* Placeholder for now as we don't track invoicing status yet */}
                        <div className="text-2xl font-bold text-muted-foreground">$0.00</div>
                    </CardContent>
                </Card>
            </div>

            {/* Team Breakdown Table */}
            <Card>
                <CardHeader>
                    <CardTitle>Team Performance</CardTitle>
                    <CardDescription>Breakdown by team member.</CardDescription>
                </CardHeader>
                <CardContent>
                    <Table>
                        <TableHeader>
                            <TableRow>
                                <TableHead>Name</TableHead>
                                <TableHead className="text-right">Total Hours</TableHead>
                                <TableHead className="text-right">Billable Hours</TableHead>
                                <TableHead className="text-right">Utilization</TableHead>
                                <TableHead className="text-right">Billable Amount</TableHead>
                            </TableRow>
                        </TableHeader>
                        <TableBody>
                            {Array.from(teamStats.values()).map((stat) => (
                                <TableRow key={stat.name}>
                                    <TableCell className="font-medium">{stat.name}</TableCell>
                                    <TableCell className="text-right">{stat.totalHours.toFixed(2)}</TableCell>
                                    <TableCell className="text-right">{stat.billableHours.toFixed(2)}</TableCell>
                                    <TableCell className="text-right">{stat.utilization.toFixed(0)}%</TableCell>
                                    <TableCell className="text-right">${stat.billableAmount.toLocaleString(undefined, { minimumFractionDigits: 2 })}</TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </CardContent>
            </Card>
        </div>
    )
}
